.header {
    width: 100%;
    min-height: 520px;
    margin-top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    position: relative;
}
.headerImg {
    position: absolute;
    bottom: 0;
    left: 0;
    max-width: 1500px;
    object-position: center;
    transform: translateY(250px);
    background-color: #0f0729c7;
    background-blend-mode: darken;
    object-fit: contain;
    z-index: 48;
}
.filter {
    position: absolute;
    z-index: 49;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #0f0729c7;
}
.header h1 {
    color: #f2f2f2;
    font-family: 'Righteous';
    font-size: 4em;
    font-weight: 500;
    animation: appearTop 2s;
    -webkit-animation: appearTop 2s;
    -moz-animation: appearTop 2s;
    position: relative;
    z-index: 50;
}
.contactsWrapper {
    font-family: 'Montserrat';
    background-color: #0a001a;
    color: #f2f2f2;
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 500px;
    flex-wrap: wrap;
}
.contactsWrapper h1 {
    width: 100%;
    text-align: center;
}
.contactBox {
    width: 330px;
    height: 500px;
    padding: 15px 25px;
}
.contactBox .right {
    padding-left: 45px;
}
.icon {
    width: 1em;
    height: 1em;
    filter: invert(100%);
    margin-right: 10px;
}
.contactPicture {
    filter: invert(100%);
    width: 250px;
    height: 250px;
}
@keyframes appearTop {
    from { 
        transform: translateY(-100px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}
@-webkit-keyframes appearTop {
    from { 
        transform: translateY(-100px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}
@-moz-keyframes appearTop {
    from { 
        transform: translateY(-100px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}